/**
 * PortfolioPage.tsx
 * @file PortfolioPage.tsx
 * @description PortfolioPage.tsx is a React component for the portfolio page of the portfolio website.
 * It is a functional component that returns a div with the class name "portfolio".
 */

/**
 * Importing necessary modules to make the component work
 */
import React, { useEffect, useRef } from "react";
import "../css/PortfolioPage.css";

/**
 * Importing images
 */
import florianC from "../images/florianC.jpg";
import FlcheBas from "../images/FlcheBas.png";
import FlcheHaut from "../images/FlcheHaut.png";
import ExperiencesDiv from "./ExperiencesDiv";
import biosphere7 from "../images/biosphere7.png";
import odomo from "../images/odomo.png";
import machine from "../images/machine.png";
import sql from "../images/SQL.png";
import web from "../images/web.png";
import swot from "../images/swot.png";
import symfony from "../images/symfony.png";

/**
 * PortfolioPage component that returns the portfolio page of the portfolio website
 * @returns PortfolioPage component
 */
const PortfolioPage: React.FC = () => {
  /**
   * Reference to the bottom of the page
   */
  const experiencesRef = useRef(null);

  useEffect(() => {
    const arrow = document.querySelector(".arrow-scroll-top");
    const nav = document.querySelector(".nav-bar");
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            if (arrow) {
              arrow.classList.add("visible");
            }
            if (nav) {
              nav.classList.add("background");
            }
          } else {
            if (arrow) {
              arrow.classList.remove("visible");
            }
            if (nav) {
              nav.classList.remove("background");
            }
          }
        });
      },
      { threshold: 0.1 }
    );
    const experience = experiencesRef.current;
    if (experience) {
      observer.observe(experience);
    }

    return () => {
      if (experience) {
        observer.unobserve(experience);
      }
    };
  }, []);

  /**
   * Function to scroll to the experiences section
   */
  const handleScrollExperiences = () => {
    const experiences = document.querySelector(".portfolio-experiences");
    if (experiences) {
      experiences.scrollIntoView({ behavior: "smooth" });
    }
  };

  /**
   * Function to scroll to the top of the page
   */
  const handleScrollTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  return (
    <div className="portfolio">
      <div className="portfolio-welcome">
        <div className="portfolio-welcome-photo">
          <img src={florianC} alt="florianC" />
        </div>
        <div className="portfolio-welcome-text">
          <h1>Bonjour ! Je m'appelle Florian Chapoulli-Pino.</h1>
          <h2>Bienvenue sur mon portfolio !</h2>
          <h2>Bonne visite.</h2>
          <div className="arrow-experiences">
            <img
              src={FlcheBas}
              onClick={handleScrollExperiences}
              alt="scroll-experiences-arrow"
            />
          </div>
        </div>
      </div>
      <div className="arrow-scroll-top">
        <img
          src={FlcheHaut}
          onClick={handleScrollTop}
          alt="scroll-top-arrow"
        />
      </div>
      <div className="portfolio-experiences" ref={experiencesRef}>
        <ExperiencesDiv
          text={
            <p>
              La ralisation de ce projet a t pour moi une relle mise en
              avant du parcours  venir dans ma carrire professionelle, et des
              moyens  mettre en place afin d'implmenter un besoin client 
              l'aide d'un langage de programmation, tout en ayant  fournir un
              rapport sur ce projet et expliquer clairement le code et son
              fontionnement.
              <br /> Il en dcoule alors pour moi de nombreux points positifs,
              comme la mise en place d'un planning optimis pour travailler en
              quipe et dans de bonne conditions, un sens des responsabilits
              car il tait important de fournir un code fonctionnel qui rpond
              aux besoins clients.{" "}
            </p>
          }
          image={odomo}
          position="left"
          onSideText="ODOMO"
        />

        <ExperiencesDiv
          text={
            <p>
              Ce projet a t pour moi trs riche en matire dapprentissage.
              Durant les deux phases qui constituaient ce projet, jai pu
              entre-autre contribuer  la mise en place de deux stratgies qui
              allaient tre attribus  une intelligence artificielle.
              <br />
              Cela m'a permis d'apprendre comment apprivoiser une tache donne,
              que ce soit le cahier des charges et les rgles dictes par
              celle-ci ou bien meme les mthodes de travail  utiliser. <br />
              Le principal gain qui dcoule de ce projet a t pour ma part
              l'veil de mon esprit logique, qui m'a permis de comprendre selon
              tel ou tel cas quelle tait la ou les actions possibles, et
              prfrables.
            </p>
          }
          image={biosphere7}
          position="right"
          onSideText="BIOSPHERE 7"
        />

        <ExperiencesDiv
          text={
            <p>
              Le  challenge  de cette SAE tait dinstaller une machine
              virtuelle pour permettre  une quipe de dev de dvelopper un
              nouveau jeu avec le langage go, quil a donc fallu installer,
              ainsi quun IDE pour sen servir. <br />
              Lenjeu tait donc ici de mettre en place une machine avec un
              hardware adapt, un compte administrateur et un compte dveloppeur
              qui ont chacun des droits dfinis selon leur statut. Il a
              galement fallu mettre en place l'outil git pour raliser un git
              clone ainsi que modifier le prompt bash pour afficher de nimporte
              o ltat de ce git. <br />
              Ainsi, ce projet nous a permis  moi et  mes camarades de
              maitriser l'installation d'une machine selon des caractristiques
              bien dfinis, ainsi que de comprendre comment fonctionne un
              terminal de commande et comment le modifier pour y indiquer des
              lements utiles.
            </p>
          }
          image={machine}
          position="left"
          onSideText="MACHINE VIRTUELLE"
        />

        <ExperiencesDiv
          text={
            <p>
              {" "}
              Ce travail de groupe dans le cadre d'une SAE m'a permis de
              dvelopper mes connaissances et de pouvoir maitriser le langage
              SQL de manire adquate. <br />
              Le dfi derrire ce projet tait de mettre en place une cohsion
              d'quipe avec son binme, de plus il a t important d'assurer le
              fait de se lguer les tches pour optimiser le temps de travail.
              Au del de cet esprit de groupe, l'lment le plus important qui
              dcoule de cette activ a t d'assurer la bonne qualit des
              donnes, de sorte  ce qu'elle soit cohrente, complete et
              prcise. <br />
              Ainsi ma connaissance des bases de donnes a t accrue, avec
              notamment le remplissage et la cration d'une base de donne, en
              passant par un schma entit-association.
            </p>
          }
          image={sql}
          position="right"
          onSideText="SQL"
        />

        <ExperiencesDiv
          text={
            <p>
              {" "}
              Voici une nouvelle fois une SAE qui m'a permis de dvelopper mon
              esprit cratif et de maitriser de manire pointue le langage html
              et css pour permettre la ralisation d'un site web en rapport avec
              une socit. <br />
              Le site qu'il tait ncessaire de produire devait donc respecter
              des critres prcis, la loi en vigueur, car dans mon cas il
              s'agissait d'une socit qui produit des boissons alcoolises.
              Dans ce cas-ci, j'ai pu apprendre les normes lgales a mettre sur
              un site internet, en plus d'avoir  crer un site ergonomique et
              pratique au client. <br />
              Cette production a t pour moi une grande lecon, qui a pu m'en
              apprendre plus sur le langage HTML et CSS, mais aussi l'ergonomie
              d'un site et les normes a respecter vis--vis de celui-ci.
            </p>
          }
          image={web}
          position="left"
          onSideText="SITE WEB"
        />

        <ExperiencesDiv
          text={
            <p>
              Ce projet m'a permis de mieux aborder ma vision d'une entreprise.{" "}
              <br />
              En effet, j'ai pu ici raliser avec mes camardes une analyse
              fonctionnelle d'une entreprise. Mais ce n'est pas tout, il a aussi
              fallu observer les diffrentes opportunits et menaces auxquelles
              elle tait confronte. <br />
              Il en dcoule alors pour moi de nombreux points positifs, comme la
              mise en place d'un planning optimis pour travailler en quipe et
              dans de bonne conditions, une maitrise des outils de recherche car
              il tait important de fournir des informations vrifis et
              pertinentes qui permet de mieux comprendre l'environnement de
              l'entreprise.
            </p>
          }
          image={swot}
          position="right"
          onSideText="SWOT"
        />

        <ExperiencesDiv
          text={
            <p>
              {" "}
              Voici une nouvelle fois une SAE qui m'a permis de dvelopper mon
              esprit cratif et de maitriser de manire pointue le langage html
              et css mais aussi php avec le framework Symfony pour permettre la
              ralisation d'une application full-stack. <br />
              L'application qu'il tait ncessaire de produire devait donc
              respecter des critres prcis, critres tant explicit autour
              d'une runion avec le Product Owner. Dans ce cas-ci, j'ai pu
              apprendre  crer un site ergonomique et pratique au client.{" "}
              <br />
              Cette production a t pour moi une grande lecon, qui a pu m'en
              apprendre plus sur le langage PHP et l'outil Symfony, mais aussi
              l'ergonomie d'un site et les normes a respecter vis--vis de
              celui-ci.
            </p>
          }
          image={symfony}
          position="left"
          onSideText="SYMFONY"
        />
      </div>
    </div>
  );
};

export default PortfolioPage;
